.App{
  height: 120vh;
  padding-top: 10px;
  background-color: #1b1b32;
  font-family: Lato,sans-serif;
  display: flex;
	flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}

.logo-contenedor {
  width: 100%;
  height: 50px;
  margin: 8px ;
  display: flex;
  align-items: center;
  justify-content: center;
}
.logo{
  height: 40px;
}
.contenedor-calculadora {
	width: 400px;
	height: 630px;
	background-color: #99c9ff;
	border-radius: 20px;
	border: 5px solid white;

}
.fila {
	margin: 3px;
	display: flex;
	align-items: center;
	justify-content: center;
}
